$navHeight: 50;

.pageHeaderNav {
  margin-top: rem-calc(-$navHeight);
  position: relative;
  z-index: 1;

  &__list {
    @extend .ul-unstyled;
    @include display(flex);
    @include flex-direction(column);
    width: 100%;

    &__item {
      @include display(flex);
      @include align-items(center);
      @include justify-content(center);
      height: rem-calc($navHeight);
      border: 1px solid $mediumGrey;
      border-top: none;
      background: $white;

      a {
        font-size: rem-calc(26*2/3);
        color: $jet;
        display: block;
        width: 100%;
        padding: 0 rem-calc(10);
        text-align: center;
      }

      &.active,
      .no-touch &:hover,
      .touch &:active,
      &:focus {
        background: $blue;
        border-color: $blue;
        a {
          color: $white;
        }
      }

      .icon {
        padding-right: rem-calc(5);
        font-size: rem-calc(30);
        line-height: rem-calc(17);
        vertical-align: middle;
      }
    }
  }
}

@media #{$small-only} {
  .pageHeaderNav {
    padding: 0;
  }
}

@media #{$medium-up} {
  .pageHeaderNav {
    &__list {
      display: table;
      width: 100%;
      table-layout: fixed;

      &__item {
        border: 1px solid $mediumGrey;
        border-left: none;
        display: table-cell;
        vertical-align: middle;

        a {
          font-size: rem-calc(14);
        }
      }
    }
  }
}

